.paralax-background{
  width: 100%;
  height: 550px;
  position: fixed;
  top: 0px;
  z-index: -1;
}

.background-content{
  height: 100%;
}
.tile-box{
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column;
  flex-wrap: wrap;
}

.tile{
  width: 33%;
  height: 269px;
  border: 1px;
  padding: 2px;
}
.tile img{
  width: auto;
  height: 100%;
  display: block;
}
@media (min-width: 1350px) {
  .tile{
    width: 25%;
  }
}
.abs-button{
  position: absolute;
}
/* flip card */
.flip-card {
  background-color: transparent;
  width: 100%;
  height: 100%;
  perspective: 1000px;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.flipped {
  transform: rotateY(180deg);
}

.flip-card-front, .flip-card-back {
  position: absolute;
  top:0px;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  overflow: hidden;
}

.flip-card-back {
  transform: rotateY(180deg);
}




.homepage .content-container{
  margin-top: 550px;
  position: absolute;
}
.homepage .content{
  position: relative;
  padding-top: 5px;
}

.prof-pic-container{
  width: 200px;
  height:200px;
  position: absolute;
  top:-100px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 1px 1px 9px;
  color:black !important;
}
#name{
  margin: 0px;
}
.profile{
  position: absolute;
  top:0px;
  width: 250px;
  padding-top: 100px;
  display: flex;
  align-items: center;
  flex-flow: column;
  height: calc(100% - 100px);
  background-color: #0842cc;
}
.profile > * {
  color: white;
}
.prof-pic{
  width: 100%;
  height: auto;
}
.sidebar{
  display: flex;
  height: 100%;
  align-items: flex-start;
  flex-flow: column;
  padding-left: 15px;
}
.sticky-sidebar{
  position: sticky; 
  top: 0px;
}
.sticky-sidebar ul{
  padding-left: 15px;
}
.homepage .content{
  background-color: white;
}
.anchor-link a{
  color:white;
}
.main{
  margin-left: 250px;
  padding-left: 15px;
  padding-right: 15px;
}


/* Hambuger Menu */
.anchor-menu{
  position: sticky;
  float: right;
  top:15px;
  right:15px;
  height: auto;
  z-index: 4;
}
.anchor-menu.open-menu{
  position: fixed;
}
.anchor-menu img{
  height: 35px;
  width: 35px;
  position: absolute;
  top: 5px;
  right: 5px;
  transition: 0.3s;
}

.dropdown-menu{
  z-index: 3;
  overflow: hidden;
  position: fixed;
  right: 0px;
  top: 0px;
  width: 70%;
  height: 100%;
  transition: 0.8s;
  background-color:rgba(255, 255, 255, .95);
  font-size: 27px;
  padding-top: 90px
}
.dropdown-menu li{
  padding-top: 10px;
}
.dropdown-menu a{
  color:black;
}
@media (min-width: 776px){
  .anchor-menu{
    visibility: hidden;
  }
}
@media (max-width: 776px){
  .main{
    margin-left:0px;
  }
  .profile{
    position: relative;
    width: 100%;
  }
  .about{
    text-align: center;
  }
  .sticky-sidebar{
    visibility: hidden;
    height: 0px;
  }

}
/* RESUME */

.resume-header{
  text-align: center;
  background-color: lightgrey;
}

/*Carousel*/

.carousel {
  position: relative;
  width: 500px;
  margin: 0 auto;
  height: auto;
  overflow: hidden;
  white-space: nowrap;
  background-color: #e0e0e0;
  border: 1px solid black;
}

.slide-wrapper {
  position: relative;
  height: 100%;
  width: 100%;
}

.slide {
  display: inline-block;
  height: 100%;
  width: 100%;
}
.slide img{
  max-width: 100%;
  max-height: 550px;
  margin-left: 50%;
  transform: translate(-50%);
}
.desc-wrapper{
  white-space: normal;
  margin:5px 15px 5px 15px;
  background-color: white;
  padding:1px 10px 1px 10px;
  border-radius: 5px;
}
.current-slide{
  position: absolute;
  top:5px;
  right: 5px;
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 7px;
  padding: 0 2px 0 2px;
}
.arrow{
  height: 50px;
  width: 50px;
  position: absolute;
  top: 50%;
  border-radius: 25px;
  background-color: rgba(255, 255, 255, 0.4);
}
.arrow img {
  width: 100%;
  height: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  transform: translateX(9px);
}

.prev-arrow{
  transform: scaleX(-1);
  left:0px; 
}
.next-arrow{
  right: 0px;
}